  font-family: "Monteserrat", "Avenir", Helvetica, Arial, sans-serif;
  font-weight: 500;
  line-height: 1.5; }

button {
  font-family: "Monteserrat", "Avenir", Helvetica, Arial, sans-serif;
  font-weight: 500; }

html,
body {
  margin: 0px;
  padding: 0px;
  max-width: 100%;
  height: 100%;
  font-size: 100%; }

html {
  color: #252537;
  background-color: #ffffff; }

ul,
li {
  padding-left: 0;
  margin-left: 0;
  list-style-type: none; }
